import React from 'react';
import { Pagination, Dropdown } from 'react-bootstrap';

export class PageDevice extends React.Component{
    constructor(props){
        super(props);
        let totalPage = this.props.totalPage || 1;
        let indexActive = this.props.indexActive || 1;
        let listCountRequest = this.props.listCountRequest || [5,10,15];
        let indexCountRequest = this.props.indexCountRequest || 0;
        this.state ={
            indexActive,
            totalPage,
            listCountRequest,
            indexCountRequest
        }
    }

    componentWillReceiveProps(nextProps){
        if(nextProps.totalPage !== this.state.totalPage){
            this.setState({totalPage: nextProps.totalPage});
        }
    }

    selectIndex(index){
        console.log('indexxxxxxxxxxxxxxx', index);
        let indexActive = this.state.indexActive;
        if(index === 'first') indexActive = 1;
        else if(index === 'prev') indexActive -=1;
        else if(index === 'next') indexActive +=1;
        else if(index === 'last') indexActive = this.state.totalPage;
        else if(index !== indexActive) indexActive = index;
        if(this.props.onChange){
            this.props.onChange(indexActive, this.state.listCountRequest[this.state.indexCountRequest]);
        }
        this.setState({indexActive});
    }

    selectCountRequest(index){
        if(this.props.onChange){
            this.props.onChange(this.state.indexActive, this.state.listCountRequest[index]);
        }
        if(index !== this.state.indexCountRequest){
            this.setState({indexCountRequest: index});
        }
    }

    render(){
        let render = [];
        for(let i = 0; i < this.state.totalPage; i++){
            if(i+1 === this.state.indexActive){
                render.push([
                    <Pagination.Item 
                        active
                        onClick={(i)=>this.selectIndex(i)}
                    >
                        {i+1}
                    </Pagination.Item>    
                ]);
                continue;
            }
            render.push([
                <Pagination.Item
                    onClick={()=> this.selectIndex(i+1)}    
                >
                    {i+1}
                </Pagination.Item>
            ]);
        }

        let dropDown = [];
        for(let i = 0; i < this.state.listCountRequest.length; i++){
            if(i !== this.state.indexCountRequest){
                dropDown.push([
                    <Dropdown.Item onClick={()=>this.selectCountRequest(i)}>{this.state.listCountRequest[i]}</Dropdown.Item>
                ]);
            }
        }
        let leftButtonGroup = ([<Pagination.First onClick={()=>this.selectIndex('first')}/>,<Pagination.Prev onClick={()=>this.selectIndex('prev')}/>]);
        let rightButtonGroup = ([<Pagination.Next onClick={()=> this.selectIndex('next')}/>,<Pagination.Last onClick={()=>this.selectIndex('last')}/>]);
        if(this.state.indexActive === 1) leftButtonGroup = ([<Pagination.First onClick={()=>this.selectIndex('first')} disabled/>,<Pagination.Prev onClick={()=>this.selectIndex('prev')} disabled/>]);
        if(this.state.indexActive === this.state.totalPage) rightButtonGroup = ([<Pagination.Next onClick={()=> this.selectIndex('next')} disabled/>,<Pagination.Last onClick={()=>this.selectIndex('last')} disabled/>]);
        
        return([
            <Pagination>
                {leftButtonGroup}
                {render}
                {rightButtonGroup}
            </Pagination>,
            <Dropdown>
                <Dropdown.Toggle variant="success">
                    {this.state.listCountRequest[this.state.indexCountRequest]}
                </Dropdown.Toggle>
                <Dropdown.Menu>
                    {dropDown}
                </Dropdown.Menu>
            </Dropdown>
            
        ])
    }
}