import Page from '../components/Page';
import React from 'react';
import { Card, CardBody, CardHeader, Col, Row, Input } from 'reactstrap';
import ReactTable from 'react-table';
import 'react-table/react-table.css';
import Api from '../utils/api';
import Utils from '../utils/utils';
import EditPost from '../components/EditPost';
import { Link } from 'react-router-dom';
import Moment from '../components/Moment';
import Config from '../utils/config';

class TablePost extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: true,
      data: [],
      error: null,
      pages: null,
      page: 0,
      pageSize: 15,
      columns: this.defaultColumns,
      categories: [],
      status: ''
    };
  }

  componentDidMount() {
    Api.getCategory('active').then(res => {
      if (res.success) {
        const categories = res.data.map(el => ({
          value: el._id,
          label: el.name
        }));
        this.setState({
          categories
        });
      }
    });
  }

  changeStatus(item, status) {
    console.log('Click item', item);
    item = this.state.data[item._index];
    Api.changeVideoStatus({ video_id: item._id, status }).then(response => {
      if (response.success) {
        this.setState({ data: this.state.data.filter(video => video._id !== item._id) });
        this.showMessage('Thay đổi thành công');
      } else {
        this.showMessage('Thay đổi thất bại');
      }
    });
  }

  getData = (state, instance) => {
    let cols = this.defaultColumns;
    if (state.status ? state.status !== 'approved' : this.state.status !== 'approved') {
      cols = this.defaultColumns.filter(item => item.accessor !== 'statistics');
    }
    const sort = state.sorted && state.sorted.map(sort => `${sort.desc ? '-' : ''}${sort.id}`).toString();
    this.setState({
      isLoading: true,
      data: [],
      pages: state.pages,
      page: state.page,
      pageSize: state.pageSize,
      columns: cols
    });
    Api.getVideos({
      status: state.status || this.state.status,
      page: state.page + 1,
      limit: state.pageSize,
      sort
    }).then(response => {
      if (response.success) {
        const data = response.data.map(item => {
          let categories = '';
          for (let i = 0; i < item.categories.length; i++) {
            if (i != 0) {
              categories += ', ';
            }
            categories += item.categories[i].category_name;
          }
          item.statistics = {
            viewCount: item.views_count,
            likeCount: item.likes_count,
            dislikeCount: item.dislikes_count,
            commentCount: item.comments_count,
            shareCount: item.shares_count
          };
          item.categoryNames = categories;
          item.thumbnail = Utils.getYoutubeThumbnail(item.youtube_id);
          return item;
        });

        this.setState({
          isLoading: false,
          pages: response.total_page,
          data
        });
      } else {
        this.setState({
          isLoading: false,
          error: 'Tải dữ liệu lỗi.'
        });
      }
    });
  };

  onReload = () => {
    this.getData(this.state);
  };

  onChange = e => {
    this.setState({ [e.target.name]: e.target.value });
    this.getData({ ...this.state, status: e.target.value });
  };

  defaultColumns = [
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Ảnh
        </span>
      ),
      accessor: d => [d.link, d.thumbnail, d.status],
      id: 'youtube',
      sortable: false,
      Cell: props => (
        <a href={props.value[0]} target='_blank'>
          <img
            src={props.value[1]}
            style={{
              width: '100%',
              maxWidth: '10em',
              maxHeight: '7em'
            }}
          />
        </a>
      ),
      maxWidth: 200
    },
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Tên video
        </span>
      ),
      accessor: d => [d.title, d._id],
      id: 'title',
      style: { overflow: 'visible', whiteSpace: 'normal', fontWeight: 'bold' },
      Cell: ({ value }) => {
        return <Link to={Config.HOST_PREFIX + '/video-detail/' + value[1]}>{value[0]}</Link>;
      }
    },
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Thể loại
        </span>
      ),
      sortable: false,
      accessor: 'categoryNames',
      maxWidth: 150,
      style: { overflow: 'visible', whiteSpace: 'normal' }
    },
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Người đăng
        </span>
      ),
      accessor: d => [d.user_name, d.user_id],
      id: 'user_name',
      Cell: props => (
        <Link to={`${Config.HOST_PREFIX}/user-detail?id=${props.value[1]}`}>
          <b>{props.value[0]}</b>
        </Link>
      ),
      style: { overflow: 'visible', whiteSpace: 'normal', maxWidth: "100px" }
    },
    {
      Header: 'Thời gian',
      accessor: 'createdAt',
      width: 100,
      Cell: ({ value }) => {
        return <Moment format='DD/MM/YYYY h:mm:ss A' date={value} style={{ display: 'block', whiteSpace: 'normal' }} />;
      }
    },
    {
      Header: 'Thống kê',
      maxWidth: 200,
      accessor: 'statistics',
      Cell: props => (
        <div>
          <label>
            Xem: <b>{Utils.getHumanReadableNumber(props.value.viewCount)}</b>
          </label>
          <br />
          <label>
            Like: <b> {Utils.getHumanReadableNumber(props.value.likeCount)}</b>
          </label>
          <br />
          <label>
            Dislike: <b> {Utils.getHumanReadableNumber(props.value.dislikeCount)}</b>
          </label>
          <br />
          <label>
            Share: <b>{Utils.getHumanReadableNumber(props.value.shareCount)}</b>
          </label>
          <br />
        </div>
      )
    }
  ];
  getTrProps = (state, rowInfo, column) => {
    if (rowInfo) {
      console.log(rowInfo.row);
      return {
        className:
          rowInfo.row._original.status === 'approved'
            ? 'border-right border-success'
            : rowInfo.row._original.status === 'pending'
            ? 'border-right border-warning'
            : 'border-right border-danger'
      };
    }
    return {};
  };
  render() {
    const { data, isLoading, columns, status, pages, categories } = this.state;
    return (
      <Page breadcrumbs={[{ name: 'Video', active: true }]} className='TablePost'>
        <Row>
          <Col>
            <Card className='mb-3'>
              <CardHeader>
                Danh sách video
                <Input
                  className=' ml-5 d-inline'
                  type='select'
                  name='status'
                  value={status}
                  style={{ width: 200 }}
                  onChange={this.onChange}
                >
                  <option value=''>All</option>
                  <option value='pending'>Pending</option>
                  <option value='approved'>Approved</option>
                  <option value='rejected'>Rejected</option>
                </Input>
              </CardHeader>

              <CardBody>
                <ReactTable
                  loading={isLoading}
                  manual
                  onFetchData={this.getData}
                  data={data}
                  columns={columns}
                  pageSizeOptions={[Config.VIDEO_PER_PAGE]}
                  defaultPageSize={Config.VIDEO_PER_PAGE}
                  pages={pages}
                  resizable={false}
                  showPageJump={true}
                  getTrProps={this.getTrProps}
                  // SubComponent={row => {
                  //   const ele = row.original;
                  //   return <EditPost ele={ele} categoryOptions={categories} onReload={this.onReload} />;
                  // }}
                />
              </CardBody>
            </Card>
          </Col>
        </Row>
      </Page>
    );
  }
}

export default TablePost;
