import React from 'react';
import {Table} from 'reactstrap';
import Config from '../utils/config';

export default class ListVideoUpload extends React.Component{
    constructor(props){
        super(props);
    }

    render(){
        let videosRender = [];
        let listVideos = this.props.data || [];
        listVideos.forEach((video, index) => {
            console.log('videossssssssss, idnexxxxxxxxxx', video, index);
            let categories = [];
            for(let i = 0; i < video.categories.length; i++){
                categories.push(<p>{video.categories[i].category_name}</p>);
            }

            let linkString = video.link.replace("www.youtube.com/watch?v=","img.youtube.com/vi/") + "/default.jpg"

            videosRender.push(
                <tr>
                    <td>{index+1}</td>
                    <td>
                        {/* <iframe width="560" height="315" 
                            src={linkString}
                            frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" 
                            allowfullscreen>
                        </iframe> */}
                        <img src={linkString} />
                    </td>
                    <td>{video.title}</td>
                    <td>{categories}</td>
                    <td>{video.status}</td>
                </tr>
            )
        });
        return(
            <Table>
                <thead>
                <tr>
                    <th>#</th>
                    <th style={{"width":"10%"}}>Video</th>
                    <th style={{"width":"30%"}}>Tên</th>
                    <th>Thể loại</th>
                    <th>Trạng thái</th>                    
                </tr>
                </thead>
                <tbody>
                    {videosRender}
                </tbody>
            </Table>
        )
    }
}