import React from 'react';
import apiUser from '../utils/api.user';

export default class VerifyByMail extends React.Component{
    constructor(props){
        super(props);
        console.log('QUERY', this.props.location.search);
        let {search} = this.props.location;
        let params = new URLSearchParams(search);
        let token = params.get('token');
        let email = params.get('email');
        console.log('TOKEN AND EMAIL VERIFY', token ,email);
        this.state = {
            token,
            email,
            message:'',
            count: 0
        }
    }

    verify(token, email){
        apiUser.verifyUser('EMAIL',token,email).then(response=>{
            if(response.success){
                let message = 'Xác thực email thành công'
                this.setState({message, count:1});
            }
            else{
                
                let message = 'XAC THUC EMAIL THAT BAI'
                this.setState({message, count:1})
            }
        }).catch(err => {
            console.log('VERIFY MAIL FAILED', err);
        })
    }

    render() {
        if(this.state.count === 0)
            this.verify(this.state.token, this.state.email);
        return(
            <h1>{this.state.message}</h1>
        )
    }
}