import Page from '../components/Page';
import React from 'react';
import { Card, CardBody, CardHeader, Col, Row, Button } from 'reactstrap';
import api from '../utils/api';
import ListGroup from '../components/GroupPermission/ListGroups';
import ListPermission from '../components/GroupPermission/ListPermission';
import AddGroupModal from '../components/AddGroupModal';
import Toast from '../components/Toast';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import ModalListAdmin from '../components/GroupPermission/ModalListAdmin';
import Selector from '../components/Selector';
import ListMember from '../components/GroupPermission/ListMembers';
import Config from '../utils/config';



// const makeButtonActive = (index) => {
//     showListGroup[index].setAttribute({active : "true"});
// }
class GroupPermission extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            isLoaded: true,
            groups: [],
            permissions: [],
            showAddGroupModal: false,
            currentGroupIndex: 0,
            listAdmin: [],
            selectedDetailOfGroup:0,
            listMember:[]
        }
        api.getAllGroupPermission().then(response => {
            console.log('RESPONE WHEN CALL PERSMSION GROUP', response);
            if(response.success){
                this.setState({groups:response.data.map(item=>{item.permissions = item.permission_ids; return item;})});
            }
            console.log('STATE', this.state.groups);
        });
        api.getPermissions().then(response => {
            console.log('RESPONE WHEN CALL PERSMSION GROUP', response);
            if(response.success){
                this.setState({permissions:response.data});
            }
        });
        api.getFreeAdmin().then(response =>{
            if(response.success){
                this.setState({listAdmin: response.data});
            }
        });
        console.log('CURRENT GROUP IN CONSTRUCTOR========>', this.state.groups[this.state.currentGroupIndex]);
        
        
    }

    addAdmin(index, data){
        let currentGroup = this.state.groups[this.state.currentGroupIndex];
        let dataUpdate = {
            admin_id: data._id,
            permission_group_id: currentGroup._id    
        }
        api.addAdminToPermissionGroup(dataUpdate).then(response => {
            if(response.success){
                let listAdmin = this.state.listAdmin;
                let listMember = this.state.listMember;
                listAdmin.splice(index,1);
                listMember.push(data);
                this.toast.showMessage("Thêm admin thành công");
                this.setState({listAdmin, listMember});
            }
            else{
                this.toast.showMessage("Thêm admin thất bại");
            }
        });
    }

    async changeGroupDetail(index){
        if(this.state.currentGroupIndex != index){
            await this.setState({currentGroupIndex: index});
        }
        
        api.getMemberOfPermissionGroup(this.state.groups[this.state.currentGroupIndex]._id).then(response=>{
            if(response.success){
                this.setState({listMember: response.data});
            }
            else{

            }
        });
    }

    showAddGroupModal(){
        console.log('Show add group modal');
        this.setState({showAddGroupModal: true});
    }

    hideAddGroupModal(){
        console.log('Hide add group modal');
        this.setState({showAddGroupModal: false});
    }

    addGroup(data){
        this.hideAddGroupModal();
        api.createPermissionGroup(data).then(response=>{
            console.log(response);
            if(response.success){
                this.toast.showMessage('Đã thêm nhóm thành công.');
                this.setState({groups: [...this.state.groups,response.data]});
            } else{
                this.toast.showMessage('Thêm nhóm thất bại'
                );
            }
        });
    }

    changeCurrentGroupPermission(permission){
        const newGroups = this.state.groups.map(item=>item);
        const current = newGroups[this.state.currentGroupIndex];
        current.permissions = permission;
        this.setState({groups: newGroups});
        api.updateGroupPermissons(current._id, current.permissions).then(response=>{
            console.log(response);
        });
    }

    onChangeDetailGroup(index, data){
        if(index !== this.state.selectedDetailOfGroup){
            this.setState({selectedDetailOfGroup:index});
        }
        if(this.state.groups[this.state.currentGroupIndex] && index === 1){
            api.getMemberOfPermissionGroup(this.state.groups[this.state.currentGroupIndex]._id).then(response=>{
                if(response.success){
                    this.setState({listMember: response.data});
                    
                }
            });
        }
    }

    deleteMemberOutOfGroup(index, data){
        console.log('index, data', index, data);
        api.adminOutGroup(data._id).then(response => {
            if(response.success){
                let listMember = this.state.listMember;
                let listAdmin = this.state.listAdmin;
                listMember.splice(index,1);
                listAdmin.push(data);
                this.setState({listMember, listAdmin})
            }
            else{
                this.toast.showMessage("delete member fail");
            }
        })
    }

    render(){
        const {currentGroupIndex, groups, permissions} = this.state;
        const currentGroup = groups.length>0?groups[currentGroupIndex]:{permissions: []};
        let detailOfGroupRender=[];
        console.log('Current group',currentGroup);
        if(this.state.selectedDetailOfGroup===0){
            detailOfGroupRender.push(
                <ListPermission
                    activedPermissions={currentGroup.permissions}
                    permissions = {permissions}
                    onChange={(permisisons)=>this.changeCurrentGroupPermission(permisisons)}
                />
            )
        }
        else if(this.state.selectedDetailOfGroup === 1){
            detailOfGroupRender.push(
                <div>
                <Col md={12} className="px-0 py-0">
                    <ModalListAdmin
                        icon="plus"
                        buttonLabel={"Thêm thành viên "}
                        listAdmin={this.state.listAdmin}
                        onChange={(index, data)=>this.addAdmin(index,data)}
                    />
                </Col>
                <ListMember
                    listMember={this.state.listMember}
                    onChange = {(index, data) => this.deleteMemberOutOfGroup(index, data)}
                />
                </div>
            );
        }
        return (
            <Page breadcrumbs={[{ name: 'Danh sách Nhóm', active: true }]} className="TablePost py-2 px-2"
          >
                <Row className="mx-0" style={{minHeight: '70vh'}}>
                    <Col className="col-4 col-md-3 col-lg-2 border py-2 px-0 bg-white" >
                        <div className="col-12 mb-3">
                        <Button color="success" block="true" onClick={()=>this.showAddGroupModal()}>
                            <FontAwesomeIcon icon="plus"/> Thêm nhóm
                        </Button>
                        </div>
                        
                        <AddGroupModal
                        open={this.state.showAddGroupModal}
                        onClose={()=>this.hideAddGroupModal()}
                        onAdd={(data)=>this.addGroup(data)}/>

                        <ListGroup
                            activeIndex={this.state.currentGroupIndex}
                            data = {this.state.groups}
                            onChange= {(index) => this.changeGroupDetail(index)} 
                        />
                    </Col>
                    <Col className="col-8 col-md-9 col-lg-10 border py-2"   style={{background: '#E9EBEE'}}>
                        <Selector
                            data={["Quyền","Thành viên"]}
                            onChange={(index, data) => this.onChangeDetailGroup(index, data)}
                        />
                        
                        {/*  */}
                        
                        <h4>Nhóm: {currentGroup.name}</h4>
                        {detailOfGroupRender}
                    </Col>
                </Row>

                {/* <ModalListAdmin buttonLabel={"abc"}/> */}
                <Toast ref={ref=>this.toast = ref}/>
            </Page>
        )
    }
}

export default GroupPermission;