import React from 'react';
import { Row, Col, Button, DropdownButton, Dropdown, Form } from 'react-bootstrap';
import Page from '../components/Page';
import DropdownItem from 'react-bootstrap/DropdownItem';
import api from '../utils/api';
import apiUser from '../utils/api.user';


const youtubeKey = 'AIzaSyAPEWJcdUOhVzq90wlYH34ENpUTW3OM8lM';
const regionCodes = ['VN', 'US', 'AU', 'KR', 'CN', 'JP'];
export default class UploadYoutubeTool extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            urlTypes: [
                {url:`https://www.googleapis.com/youtube/v3/videos?part=snippet,contentDetails&chart=mostPopular&hl=vi&key=${youtubeKey}`,name:'Top thịnh hành', type:'mostPopular'},
                {url:`https://www.googleapis.com/youtube/v3/search?part=snippet&type=video&key=${youtubeKey}`,name: 'Tìm kiếm', type:'search'}],
            index: {
                url: 0,
                category: 0,
                region: 0
            },
            maxResults: 50,
            regionCodes,
            categories: [],
            searchKey:'',
            message:''
        }
    }

    componentDidMount(){
        api.getCategory().then(response=>{
            if(response.success){
                this.setState({categories: response.data});
            }
            else{
                console.log('CATEGORIES ERROR', response);
            }
        })
    }

    select(selectIndex, key){
        let {index} = this.state;
        if(index[key] !== selectIndex) {
            index[key] = selectIndex;
            this.setState({index});
        }
    }
    inputSearchKey(input){
        console.log(input);
        this.setState({searchKey: input})
    }

    onPost(){
        this.setState({message:'đợi xíu'});
        let {urlTypes,index,regionCodes, categories, searchKey} = this.state;
        let data = {
            url: urlTypes[index.url].url,
            url_type: urlTypes[index.url].type,
            regionCode: regionCodes[index.region],
            maxResults: 50,
            videoCategoryId: categories[index.category].code,
            search_key: searchKey
        }
        console.log('DATA POST', data);
        apiUser.postManyVideo(data).then(response=>{
            if(response.success) {
                this.setState({message:'ok, check list videos de xem chi tiet'});
            }
            else {
                this.setState({message:'loi cmnr, ' + response});
            }
        })
    }



    render(){
        
        let {urlTypes, index,message, regionCodes, categories} = this.state;
        let disableInput = index.url === 0? true : false; // mostpopular not neet search key
        
        let dropItems = [], categoryItems = [], regionItems=[];
        urlTypes.forEach((url, index)=>{
            dropItems.push(
                <Dropdown.Item  onClick={()=>this.select(index, 'url')}>
                    {url.name}
                </Dropdown.Item>
            )
        });
        categories.forEach((category, index)=>{
            categoryItems.push(
                <Dropdown.Item onClick={()=>this.select(index,'category')}>
                    {category.name}
                </Dropdown.Item>
            )
        });
        regionCodes.forEach((region, index)=>{
            regionItems.push(
                <Dropdown.Item onClick={()=>this.select(index,'region')}>
                    {region}
                </Dropdown.Item>
            )
        })


        return(
            

            <Form className="ml-3 mt-3">
                <Form.Group as={Row} controlId="formPlaintextEmail">
                    <Form.Label column sm="2">
                    ....:
                    </Form.Label>
                    <Col sm="10">
                    <DropdownButton size={"block"} title={urlTypes[index.url].name}>
                        {dropItems}
                    </DropdownButton>
                    </Col>
                </Form.Group>

                <Form.Group as={Row} controlId="formPlaintextPassword">
                    <Form.Label column sm="2">
                    Chọn thể loại
                    </Form.Label>
                    <Col sm="10">
                        <DropdownButton size={"block"} disabled={disableInput} title={categories[index.category]? categories[index.category].name : ""}>
                            {categoryItems}
                        </DropdownButton>                    
                    </Col>
                </Form.Group>
                <Form.Group as={Row} controlId="formPlaintextPassword">
                    <Form.Label column sm="2">
                        Chọn nước:
                    </Form.Label>
                    <Col sm="10">
                        <DropdownButton size={"block"} title={regionCodes[index.region]}>
                            {regionItems}
                        </DropdownButton>                        
                    </Col>
                </Form.Group>
                <Form.Group as={Row}>
                    <Form.Label column sm="2">
                    Từ khóa:
                    </Form.Label>
                    <Col sm="10">
                        <Form.Control disabled={disableInput} onChange={(event)=>this.inputSearchKey(event.target.value)}></Form.Control>
                    </Col>
                    
                </Form.Group>
                <Button onClick={()=>this.onPost()}>Submit</Button>

                <div className="text-center">
                    <span>{message}</span>
                </div>
            </Form>
        )
    }
}