import React from 'react';
import { ListGroup } from 'react-bootstrap';

export default class UserChoosed extends React.Component{
    constructor(props){
        super(props);
        let listUser = props.listUser ? props.listUser : [];
        this.state = {
            listUser,
            selectedIndex : 'none'
        }
    }

    componentWillReceiveProps(nextProps){
        if(nextProps.listUser){
            this.setState({listUser: nextProps.listUser});
        }
    }

    select(index){
        // console.log('SELECT', i);
        if(index !== this.state.selectedIndex){
            this.setState({selectedIndex: index});
        }
        if(this.props.onChange){
            this.props.onChange(index, this.state.listUser[index]);
        }
    }
    
    render(){
        var listUser = this.state.listUser;
        var usersShow = [];
        for(let i = 0; i < listUser.length; i++){
            let variant = 'none'
            if(i === this.state.selectedIndex){
                variant="primary"
            }
            usersShow.push(
                <ListGroup.Item
                    style={{cursor: "pointer"}}
                    as="li"
                    key={listUser[i]._id.toString()}
                    action
                    variant = {variant}
                    onClick={() => this.select(i)}
                >
                    {listUser[i].name}
                </ListGroup.Item>
            )
        }
        return (
            <ListGroup as="ul">
              {usersShow}
              <ListGroup.Item
                key={200}
                action
                variant="success"
                onClick={() => this.select("new")}
              >
                  Thêm
              </ListGroup.Item>
            </ListGroup>
        );
    }
}