import React from 'react';
import { Button, Table, InputGroup, FormControl } from 'react-bootstrap';
import {Input} from 'reactstrap';

export default class ListConfig extends React.Component{
    constructor(props){
        super(props);
        let valueInput = this.props.valueInput || '';
        this.state = {
            valueInput
        }
        this.inputValue = this.inputValue.bind(this);
    }

    componentWillReceiveProps(nextProps){
        if(nextProps.valueInput !== this.state.valueInput){
            this.setState({valueInput: nextProps.valueInput});
        }
    }

    select(index, data, action){
        if(this.props.onChange){
            this.props.onChange(index,data,action, this.state.valueInput);
        }
    }

    inputValue(value){
        console.log('valueeeeeeeeeeeeeeeeeeeeeeeee', value);
        this.setState({valueInput: value}); 
    }

    render(){
        let listConfig = this.props.data || [];
        let indexEdit = this.props.indexEdit || 0;
        // let valueInput = this.props.valueInput || '';
        // this.setState({valueInput});
        let renders = [];

        listConfig.forEach((config,index)=>{
            
            let buttonAttribute = {variant: "success", name: "Chỉnh sửa", action: ""}
            let valueAttribute = config.value;
            if(index === indexEdit){
                buttonAttribute = {variant: "secondary", name: "Lưu", action: "save"};
                valueAttribute = [
                    <Input 
                        type="text" 
                        value={this.state.valueInput} 
                        onChange={(event) =>  this.inputValue(event.target.value)}
                    />
                ];
            }
            renders.push(
                <tr key={index}>
                    <td>{index+1}</td>
                    <td>{config.name}</td>
                    <td>
                        {valueAttribute}       
                    </td>
                    <td>
                        <Button
                            variant={buttonAttribute.variant}
                            onClick={()=>this.select(index, config, buttonAttribute.action)}
                        >
                            {buttonAttribute.name}
                        </Button>
                    </td>
                    
                </tr>
            )
        })
        return(
            <Table>
                <thead>
                    <tr>
                        <th>#</th>
                        <th>Tên</th>
                        <th>Giá trị</th>
                        <th></th>
                        
                    </tr>
                </thead>
                <tbody>
                    {renders}
                </tbody>
            </Table>
        )
    }
}