import Page from '../components/Page';
import React from 'react';
import { Card, CardBody, CardHeader, Col, Row, Table, Button } from 'reactstrap';
import ReactTable from 'react-table';
import 'react-table/react-table.css';
import { Link } from 'react-router-dom';
import Api from '../utils/api';
import Utils from '../utils/utils';
import queryParser from 'query-string';
import Moment from '../components/Moment';
import Config from '../utils/config';

const columns = [
  {
    Header: () => (
      <span>
        <i className='fa-tasks' /> Ảnh đại diện
      </span>
    ),
    accessor: 'user_avatar',
    width: 80,
    sortable: false,
    Cell: props => {
      return (
        <img
          className='border rounded-circle'
          width={56}
          height={56}
          src={Utils.getAvatarOfUser(props.value) || require('../assets/img/default_user_avatar.png')}
        />
      );
    }
  },
  {
    Header: () => (
      <span>
        <i className='fa-tasks' /> Tên
      </span>
    ),
    accessor: d => [d.user_name, d._id],
    id: 'user_name',
    width: 150,
    Cell: ({ value }) => {
      return (
        <Link to={`${Config.HOST_PREFIX}/user-detail?id=${value[1]}`}>
          <div style={{ wordWrap: 'break-word', whiteSpace: 'normal' }}>
            <b>{value[0]}</b>
          </div>
        </Link>
      );
    }
  },
  {
    Header: () => (
      <span>
        <i className='fa-tasks' /> Video
      </span>
    ),
    width: 300,
    accessor: 'video_id',
    id: 'video_id',
    Cell: props => {
      //   return 1;
      return props.value ? (
        <Link to={Config.HOST_PREFIX + '/video-detail/' + props.value._id}>
          <b style={{ wordWrap: 'break-word', whiteSpace: 'normal' }}>{props.value.title}</b>
        </Link>
      ) : (
        <></>
      );
    }
  },

  {
    Header: () => (
      <span>
        <i className='fa-tasks' /> Nội dung
      </span>
    ),
    accessor: 'content',
    Cell: props => <div style={{ wordWrap: 'break-word', whiteSpace: 'normal' }}>{props.value}</div>
  },

  {
    Header: () => (
      <span>
        <i className='fa-tasks' /> Ngày bình luận
      </span>
    ),
    accessor: 'updatedAt',
    width: 125,
    Cell: props => <Moment date={props.value} style={{ display: 'block', whiteSpace: 'normal' }} />
  }
];

class Table_UserCmt extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: true,
      pages: null,
      page: 0,
      pageSize: 10,
      data: []
    };
  }
  getData = state => {
    this.setState({
      isLoading: true,
      page: state.page,
      pageSize: state.pageSize
    });
    const sort = state.sorted && state.sorted.map(sort => `${sort.desc ? '-' : ''}${sort.id}`).toString();
    Api.getComments({ limit: state.pageSize, page: state.page + 1, sort }).then(response => {
      if (response.success) {
        const data = response.data;
        this.setState({
          isLoading: false,
          data: data.filter(item => item.status != 'rejected'),
          pages: response.total_page
        });
      } else {
        this.setState({
          isLoading: false,
          error: 'Tải dữ liệu lỗi.'
        });
      }
    });
  };

  getType(query) {
    const parsed = queryParser.parse(query);
    return parsed.type;
  }

  delete(index) {
    console.log('delete', index);
    const comment = this.state.data[index];
    if (!comment) {
      return;
    }
    Api.updateComment({ id: comment._id, status: 'rejected' }).then(response => {
      if (response.success) {
        this.setState({
          data: this.state.data.filter(item => item._id != comment._id)
        });
      }
    });
  }

  render() {
    const { isLoading, data, page, pageSize, pages } = this.state;
    return (
      <Page breadcrumbs={[{ name: 'tables', active: true }]} className='TablePost'>
        <Row>
          <Col>
            <Card className='mb-3'>
              <CardHeader>Quản lý bình luận</CardHeader>
              <CardBody>
                <ReactTable
                  data={data}
                  loading={isLoading}
                  columns={columns}
                  manual
                  onFetchData={this.getData}
                  defaultPageSize={15}
                  pages={pages}
                  page={page}
                  pageSize={pageSize}
                  SubComponent={row => {
                    return (
                      <div className='mx-0 row justify-content-end'>
                        {/* <div class="action-left">
                                                    <Button color="warning">Sửa</Button>
                                                </div> */}
                        <div className='mx-3 mb-2'>
                          <Button onClick={() => this.delete(row.index)} color='danger'>
                            Xóa
                          </Button>
                        </div>
                      </div>
                    );
                  }}
                />
              </CardBody>
            </Card>
          </Col>
        </Row>
      </Page>
    );
  }
}

export default Table_UserCmt;
