import React from 'react';
import { Table, Col, Button } from 'react-bootstrap';
import api from '../utils/api';
import Toast from '../components/Toast';
import { PageDevice } from '../components/Layout/PageDevice';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import DropDown from '../components/DropDown';
import ModalHandle from './ModalHandle';
import {Link} from 'react-router-dom';
import Video from './Video';
import UserDetail from './UserDetail';
import utils from '../utils/utils';
import Config from '../utils/config';

const videoStatus = [
    {name:'Approved', _id:'approved'},
    {name:'Rejected', _id:'rejected'},
    {name:'Pending', _id:'pending'}
]

const videosStatusName = ['approved','rejected', 'pending'];
    
export default class TableVideos extends React.Component{
    constructor(props){
        super(props);
        let data = [];
        let categories = [];
        let listCountRequest = [5,10,15,20];
        this.state = {
            limit: listCountRequest[1],
            page:1,
            data,
            listCountRequest,
            totalPage: 1,
            sort: '-createdAt',
            sortStatus: 1,
            categories,
            idCategorySelected: null,
            indexCategorySelected: 0,
            statusSelected: null,
            indexStatusSelected: 0,
            modal: false,
            idDataModalSelected: null,
            component: null
        } 
    }

    componentDidMount(){
        let query = {
            limit : this.state.limit,
            page : 1,
            sort: this.state.sort
        }
        Promise.all([
            api.getVideos(query), 
            api.getCategory()
        ]).then(response=>{
            if(response[0].success && response[1].success){
                this.setState({
                    data: response[0].data,
                    totalPage: response[0].total_page,
                    categories: response[1].data
                });
            }
            else{
                this.toast.showMessage('lỗi');
            }
        });
    }

    componentWillUpdate(){
        console.log('conponent has updated');
    }

    componentWillReceiveProps(nextProps){
        if(nextProps.data && nextProps.data.length !== this.state.data.length){
            this.setState({data: nextProps.data});
        }
    }

    onChangePage(pageIndex, count){
        let query = {
            limit : count,
            page : pageIndex ,
            sort: this.state.sort,
            status: this.state.statusSelected
        }
        api.getVideos(query).then(response => {
            if(response.success){
                this.setState({data: response.data, totalPage: response.total_page, limit: count, page: pageIndex});
            }
            else{
                this.toast.showMessage('cannot get videos');
            }
        })
    }

    filter(key){
        let sort = '';
        if(key !== this.state.sort){ 
            sort = key;
        }
        else {
            sort = this.state.sort;
            if(sort.indexOf('-') === 0) sort = sort.substring(1,sort.length);
            else sort = '-' + sort;
        }
        let query ={
            limit : this.state.limit,
            page : this.state.page,
            sort,
            // categories: this.state.categories,
            status: this.state.statusSelected
        }
        api.getVideos(query).then(response => {
            if(response.success){
                this.setState({data: response.data, totalPage: response.total_page, sort: sort});
            }
            else{
                this.toast.showMessage('can not get videos')
            }
        });
    }

    selectStatus(index, id){
        if(index !== this.state.indexStatusSelected){

            let state = this.state;
            let query = {
                limit: state.limit,
                page: state.page,
                sort: state.sort,
            }
            if(state.idCategorySelected) query.categories = state.idCategorySelected;
            if(id) query.status = id;
            api.getVideos(query).then(response => {
                if(response.success){
                    this.setState({data: response.data, totalPage:response.total_page , statusSelected: query.status || null, indexStatusSelected: index});
                }
                else{
                    this.toast.showMessage('get video error')
                }
            });
        }
    }

    onChangeStatusVideo(index, status, video_id, indexVideo){
        let dataUpdate = {
            video_id,
            status
        }
        api.changeVideoStatus(dataUpdate).then(response=>{

            if(response.success){
                let datas = this.state.data;
                datas[indexVideo].status = status;
                this.setState({data : datas});
                this.toast.showMessage('update status success');
            }
            else{
                this.toast.showMessage('update status videos false');
            }
        })
    }

    selectCategory(index, id){
        if(index !== this.state.indexCategorySelected){
            let state = this.state;
            let query = {
                limit: state.limit,
                page: state.page,
                sort: state.sort,
            }
            if(state.statusSelected) query.status = state.statusSelected;
            if(id) query.categories = id;
            api.getVideos(query).then(response=>{
                if(response.success){
                    this.setState({data: response.data, totalPage: response.total_page, idCategorySelected: query.categories || null, indexCategorySelected: index});
                }
                else{
                    this.toast.showMessage('get videos error');
                }
            })
        }
    }

    onChangeModal(id,component){
       let show = true;
       let idDataModalSelected = id;
        this.setState({show, idDataModalSelected, component});
    }

    closeModal(){
        this.setState({show:false})
    }

    render(){
        let datas = this.state.data;
        let listCategories = [{name: 'All', _id:null},...this.state.categories];
        let render = [];
        let listVideosStatusFilter = [{name:'All', _id:null},...videoStatus];

        datas.forEach((data,indexVideo) => {
            let img = `https://img.youtube.com/vi/${data.youtube_id}/default.jpg`;
            let subCategories=[];
            for(let i = 0; i < data.categories.length; i++){
                subCategories.push(<p>{data.categories[i].category_name}</p>);
            }

            let defaultSelected =  videosStatusName.indexOf(data.status.toString());
            let variant = data.status == 'rejected' ? 'danger' : data.status == 'approved' ? 'success' : 'warning';
            render.push([
                <tr key={indexVideo}>
                    <td><a href={data.link} target="blank"><img src={img}/></a></td>
                    <td><a href="#" onClick={()=>this.onChangeModal(data._id, 'video')}>{data.title}</a></td>
                    <td>{subCategories}</td>
                    <td><a href="#" onClick={()=>this.onChangeModal(data.user_id, 'user')}>{data.user_name}</a></td>
                    <td>{data.views_count}</td>
                    <td>{data.comments_count}</td>
                    <td>
                        <DropDown
                            variant={variant}
                            data = {videoStatus}
                            defaultSelected={defaultSelected}
                            onChange={(index, status)=>this.onChangeStatusVideo(index,status, data._id, indexVideo)}
                            notShow="pending"
                        />
                        {/* {data.status} */}
                    </td>
                    <td>{data.createdAt}</td>
                </tr>
            ])
        });

        let modalRender = [];
        if(this.state.component === 'user'){
            modalRender.push(<UserDetail id={this.state.idDataModalSelected}/>)
        }
        else if(this.state.component ==='video'){
            modalRender.push(<Video video_id={this.state.idDataModalSelected} />)
        }

        return ([
            <Table>
                <thead>
                    <tr>
                        <th>Ảnh</th>
                        <th style={{"width":"300px"}} >Tên video</th>
                        <th>Thể loại</th>
                        <th>Người đăng</th>
                        <th>Lượt xem</th>
                        <th>Bình luận</th>
                        <th>Trạng thái</th>
                        <th>Thời gian</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td></td>
                        <td>
                            <Button 
                                onClick={()=>this.filter('title')}
                                variant="primary" 
                                className="button-style"
                            >
                                <FontAwesomeIcon icon="filter"/>
                            </Button>
                        </td>
                        <td id="categories">
                            <DropDown
                                data={listCategories}
                                onChange = {(index, id)=> this.selectCategory(index, id)}
                            />
                        </td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td id="status">
                            <DropDown
                                data={listVideosStatusFilter}
                                onChange={(index, id)=>this.selectStatus(index ,id)}
                            />
                        </td>
                        <td>
                            <Button  
                                onClick={()=>this.filter('createdAt')}
                                variant="primary" 
                                className="button-style"
                            >
                                <FontAwesomeIcon icon="filter"/>    
                            </Button>
                        </td>
                    </tr>
                    {render}
                </tbody>
            </Table>,
            <Col className="col-md-6 offset-md-3" style={{"display":"flex"}}>
                <PageDevice
                    totalPage={this.state.totalPage}
                    indexActive = {1}
                    listCountRequest = {this.state.listCountRequest}
                    indexCountRequest = {1}
                    onChange={(pageIndex, count)=>this.onChangePage(pageIndex, count)}
                />
            </Col>,
            <ModalHandle
                show={this.state.show}
                // id={this.state.idDataModalSelected}
                onChange={()=>this.closeModal()}
                // component={this.state.component}
                // component={<Video />}
            >
                {modalRender}
            </ModalHandle>
            ,
            <Toast
                ref={(ref) => this.toast = ref}
            />
        ])
    }
}